import React, {useContext, useEffect, useState} from 'react';
import Logo from "../assets/images/logo.png";
import {Link} from "react-router-dom";
import {fetchQuestions, getStoredQuestions} from "../db";
import {useOnline} from "../video.page";
import {SurveyContext} from "../surveyContext";
import VideoModal from "../video-model";


const Documentaries = () => {
    const isOnline = useOnline();
    const {surveyCompleted} = useContext(SurveyContext);
    const [videos, setVideos] = useState([]);
    const [questions, setQuestions] = useState([]);
    const [modalOpen, setModalOpen] = useState(false);
    const [selectedVideoUrl, setSelectedVideoUrl] = useState('');

    const openModal = (videoUrl) => {
        setSelectedVideoUrl(videoUrl);
        setModalOpen(true);
    };

    const closeModal = () => {
        setSelectedVideoUrl('');
        setModalOpen(false);
    };
    useEffect(() => {
        async function fetchCachedVideo() {
            const cacheName = 'my-video-cache'; // Replace with your cache name from the service worker
            const videoUrl1 = '/videos-cache/documentaries/7-animaux-sous-marins-improbables_J762HeqG.mp4'; // Replace with the actual video file path
            const videoUrl2 = '/videos-cache/documentaries/L\'intellignece du poulpe.mp4'; // Replace with the actual video file path
            const videoUrl3 = '/videos-cache/documentaries/La Rsilience des Lionnes du Ruha.mp4'; // Replace with the actual video file path
            const videoUrl4 = "/videos-cache/documentaries/Les richesses de l'Afrique.mp4"; // Replace with the actual video file path
            const videoUrl5 = '/videos-cache/documentaries/Ruse et Opportunisme _ La Survie des Chacals sur la Cote.mp4'; // Replace with the actual video file path

            const videos = [
                {
                    url: videoUrl1,
                    thumbnail: '/videos-cache/documentaries/thumbnail-video-1.png',
                    title: "7-animaux-sous-marins-improbables_J762HeqG"
                },
                {
                    url: videoUrl2,
                    title: "L'intellignece du poulpe",
                    thumbnail: '/videos-cache/documentaries/thumbnail-video-2.png',
                },
                {
                    url: videoUrl3,
                    title: "La Rsilience des Lionnes du Ruha",
                    thumbnail: '/videos-cache/documentaries/thumbnail-video-3.png',
                },
                {
                    url: videoUrl4,
                    title: "Les richesses de l'Afrique",
                    thumbnail: '/videos-cache/documentaries/thumbnail-video-4.png',
                },
                {
                    url: videoUrl5,
                    title: "Ruse et Opportunisme _ La Survie des Chacals sur la Cote",
                    thumbnail: '/videos-cache/documentaries/thumbnail-video-5.png',
                },

            ]
            setVideos(videos)
        }

        fetchCachedVideo();
    }, []);

    useEffect(() => {
        const fetchSurveyQuestions = async () => {
            if (isOnline) {
                await fetchQuestions();
            }

            const storedQuestions = await getStoredQuestions();


            setQuestions(storedQuestions);
        };

        fetchSurveyQuestions();
    }, [isOnline]);
    return (
        <>
            {surveyCompleted ?
                <>
                    <div className={"mb-10"}>
                        <nav style={{textAlign: "center"}}>
                            <img src={Logo} style={{width: '200px', height: "200px", margin: "auto"}} alt={"logo"}/>
                        </nav>
                        <div className={"my-10"}>
                            <Link to={'/video'}>
                                <button className={"text-3xl bg-indigo-400 text-white px-2 py-1 rounded-lg"}>
                                    {"<< Retour  lAccueil"}
                                </button>
                            </Link>
                        </div>
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                            {videos.map((video, index) => (
                                <div key={index} className="bg-white p-4 rounded shadow-md">
                                    <button onClick={() => openModal(video.url)}>
                                        <img src={video.thumbnail} alt={video.title} className="w-full h-auto"/>
                                    </button>
                                    <p className="mt-2 text-center font-semibold text-2xl">{video.title}</p>
                                </div>
                            ))}
                            {modalOpen && (
                                <VideoModal videoUrl={selectedVideoUrl} onClose={closeModal}/>
                            )}
                        </div>
                    </div>
                </> : <div className={"absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2"}>
                    <nav style={{textAlign: "center"}}>
                        <img src={Logo} style={{width: '200px', height: "200px", margin: "auto"}} alt={"logo"}/>
                    </nav>
                    <div className={"px-5 py-4 text-center bg-amber-50 rounded-2xl"}>
                        <p>
                            POUR ACCDER AUX VIDEOS/MUSIQUE MERCI DE PRENDRE 5 SECONDES POUR REPONDRE A UNE QUESTION
                        </p>

                        <div>
                            <Link to={'/'}>
                                <button className={"bg-indigo-400 rounded-2xl px-3 py-2 my-4 text-white"}>
                                    Accder aux videos
                                </button>
                            </Link>
                        </div>

                    </div>
                </div>}
        </>

    );
};

export default Documentaries;
