import CloseIcon from "@mui/icons-material/Close";
import * as React from "react";
import {useEffect, useState} from "react";
import {addAdvertisementView, db, fetchLatestAdvertisement, syncAdvertisementViews} from "../../db";
import {useMutation, useQuery} from "@tanstack/react-query";
import {useModalStore} from "../../store/modalStore";
import {useNavigate} from "react-router-dom";
import {useLiveQuery} from "dexie-react-hooks";

const AdPopup = ({setAdPopupOpen}) => {
    const {nextUrl} = useModalStore()
    const navigate = useNavigate()
    const [timeToSkip, setTimeToSkip] = useState(10);
    const {isPending, data} = useQuery({
        queryKey: ['advertisement'],
        queryFn: fetchLatestAdvertisement
    })

    const mutation = useMutation({
        mutationFn: () => syncAdvertisementViews()
    });

    const [canBeSkipped, setCanBeSkipped] = useState(false)
    const cachedAd = useLiveQuery(() => db.advertisements.toCollection().desc('id').first())?.name ?? "advertisement-example.mp4"

    useEffect(() => {
        if (data !== undefined) {
            addAdvertisementView(data)
            mutation.mutate()
        }
    }, [data])

    useEffect(() => {
        setCanBeSkipped(false)
        setTimeout(() => {
            setCanBeSkipped(true)
        }, 10000)

        mutation.mutate()
    }, []);

    useEffect(() => {
        const timer = timeToSkip > 0
            && setInterval(() => setTimeToSkip(timeToSkip - 1), 1000)
        return () => clearInterval(timer)
    }, [timeToSkip]);

    const handleSkip = () => {
        if (nextUrl) {
            navigate(nextUrl)
        }
        if (setAdPopupOpen) {
            setAdPopupOpen(false)
        }
    }

    if (isPending) {
        return <span>Loading...</span>
    }

    return (
        <div style={{position: "absolute", top: 0, left: 0, backgroundColor: "black", width: "100vw", height: "100vh"}}>
            <div className={"text-right mb-3"}
                 style={{
                     backgroundColor: "black",
                     position: "absolute",
                     zIndex: "999",
                     right: "5%",
                     bottom: "10%",
                     color: "white",
                     padding: "20px",
                     display: "flex",
                     alignContent: "center",
                     justifyContent: "center",
                     fontSize: "2.5em",
                     fontWeight: "bold"
                 }}>
                {
                    timeToSkip > 0 && !canBeSkipped ?
                        <button disabled>{timeToSkip} secondes pour passer</button>
                        :
                        <button onClick={handleSkip}>Passer la publicit<CloseIcon/></button>
                }
            </div>
            <video autoPlay style={{width: "100%", height: "100%"}}>
                <source src={`advertisement/${data.link ?? cachedAd}`} type="video/mp4"/>
            </video>
        </div>
    )
}

export default AdPopup
